<template>
    <div class="h-full flex-col gap-3 bg-inherit">
        <div class="w-full px-4 py-5 rounded-xl bg-white overflow-hidden my-2 shadow-md"
         v-for="cuentamaster in listaCuentasMaestras" :key="cuentamaster.id">
            <p class="py-1"><span class="font-bold pr-2">Usuario: </span><span>{{ cuentamaster.usuario }} </span></p>
            <p class="py-1"><span class="font-bold pr-2">Empresa:</span><span>{{ cuentamaster.nombre }}</span></p>
            <p class="py-1"><span class="font-bold pr-2">Codigo empresa: </span><span>{{ cuentamaster.codempresa }}</span></p>
            <p class="py-1"><span class="font-bold pr-2">Dirección: </span><span>{{ cuentamaster.direccion }}</span></p>
            <p class="py-1"><span class="font-bold pr-2">Ruc:</span> <span>{{ cuentamaster.ruc }}</span></p>
            <p class="py-1"><span class="font-bold pr-2">Telefono:</span> <span>{{ cuentamaster.contacto }}</span></p>
            <div class="py-3"><span class="pr-4 font-bold">Acciones:</span>
                <a href="#" class="mr-2 p-2 font-medium text-white bg-green-500 
                hover:bg-green-700 ease-in duration-300 rounded-lg"
                    @click.prevent="editarCuentaMaster(cuentamaster.id, cuentamaster.id_cuentamaestra)">
                    Editar
                </a>
                <a href="#" class="p-2 font-medium text-white  bg-red-500 hover:bg-red-700 ease-in duration-300 rounded-lg"
                    @click.prevent="eliminarCuentaMaster(cuentamaster.id, cuentamaster.id_cuentamaestra)">Eliminar</a>
            </div>
        </div>

    </div>
</template>
<script>
import { defineComponent } from "vue";

export default defineComponent({
    name: "ItemsTableMobil",
    props: {
        listaCuentasMaestras: Array
    },
    methods: {
        eliminarCuentaMaster(idUsuario, idCuentaMaster){
            this.$emit('eliminar-cuentamaster', idUsuario, idCuentaMaster)
        },
        editarCuentaMaster(idUsuario, idCuentaMaster){
            this.$emit('editar-cuentamaster', idUsuario, idCuentaMaster)
        }
    }
})
</script>